body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-color: #E9EBE5;
  font-family: poppins;
  height: 100vh;
  padding: 0 0 11rem 0;
  text-align: center;
}



.Header {
	align-items: center;
	/*background-color: #03ad03;*/
	background: url(/disc-golf-guru/static/media/disc-golf-field-2.a9495e28.jpg);
	background-repeat: no-repeat;
  background-position: center;
  background-size: 110%;
  background-position-x: -5rem;
	color: white;
	display: flex;
	justify-content: center;
	height: 12rem;
	margin: 0;
	width: 100%;
	/*font-family: 'Pacifico', cursive;*/
	/*font-family: 'Open Sans', sans-serif;*/
	font-family: 'Holtwood One SC', serif;
	/*font-family: 'Sriracha', cursive;*/
}

.NavLink-site-title {
	text-decoration: none;
	color: #FFF;
}

.site-title {
	font-size: 3rem;
	background-color: rgba(255,255,255, 0);
	/*padding: 0 5rem 0 0;*/
	text-shadow: #000 5px 5px 5px;
}
.CardContainer {
  align-items: center;
  background-color: #FFF;
  border: 3px solid #febb00;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  height: auto;
  justify-content: center;
  margin: 1.25rem auto;
  padding: 0.75rem 0 1rem 0;
  width: 90%;
  box-shadow: 0px 0px 1px 2px #Feeb00;
}
.CourseInfoCard {
	border: 3px solid #febb00;
  color: #524949;
	width: 20rem;
  text-align: center;
	margin: 1.25rem auto;
	height: 24rem;
	background-color: #FFF;
	border-radius: 20px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 0 0 2rem 0;
  box-shadow: 0px 0px 1px 2px #Feeb00;
}

.header-container {
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 85%;
	margin: 1.25rem 0 0;
}

.favorite-btn {
	border-radius: 50%;
	width: 3rem;
	height: 3rem;
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
	border: none;
	justify-self: right;
	margin: 0 0.25rem 0 0;
  outline-width: 0;
}

.favorite-btn:hover {
	background-color: #2c9a2c;
}

.favorite-btn:hover > .fa-heart {
	background-color: #2c9a2c;	
	color: #febb00;
}

.fav-btn-active {
	background-color: #2c9a2c;	
	color: #febb00;
}

.fav-btn-inactive {
	background-color: #febb00;	
	color: #febb00;
}

.fa-heart {
	color: #2c9a2c;
	font-size: 1.5rem;
}

.heart-active {
	color: #febb00;
}

.heart-inactive {
	color: #2c9a2c;
}

.course-name {
  color: #2c9a2c;
  font-size: 1.25rem;
  font-weight: 600;
  overflow-wrap: break-word;
	width: 77%;
}

.course-address {
  font-size: 1.1rem;
  line-height: 0.75rem;
	margin: 1.25rem 0 0;
}

.address-fields {
	margin: 1rem 0;
}

.course-info-header {
	font-weight: 700;
}

.course-info {
	margin: 0 0 0.5rem;
}

.course-info-link {
	display: flex;
	flex-direction: column;
	margin: 0.4rem 0;
}

.review-link {
  color: #2c9a2c;
	margin: 0.8rem 0;
}
.Error {
  align-items: center;
  background-color: #FFF;
  border: 3px solid #febb00;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  height: 9rem;
  justify-content: center;
  margin: 1.25rem auto;
  padding: 0.75rem 0 1rem 0;
  width: 38rem;
  box-shadow: 0px 0px 1px 2px #Feeb00;
}

.error-header {
  font-size: 1.25rem;
  font-weight: 600;
  color: #2c9a2c;
  margin: 1rem 4rem 0;
}

.error-message {
  margin: 1rem 4rem;
  line-height: 1.25rem;
}

.Nav {
  align-items: center;
    cursor: pointer;
  background-color: #febb00;
  background-color: #FFF;
  display: flex;
  justify-content: center;
  padding: 0.7rem 0 1rem 0;
  width: 100%;
}
.nav-btn {
  border-radius: 10px;
  cursor: pointer;
  font-family: poppins;
  font-size: 1.25rem;
  font-weight: 600;
  height: 3rem;
  margin: 0.5rem 1rem 0.25rem;
  outline-width: 0;
  width: 13rem;
}

.nav-btn:hover {
  background-color: #76ce76;
  color: #382626;
}

.nav-btn-active {
  background-color: #febb00;
  color: #302a2a;
  outline-width: 0;
}

.nav-btn-inactive {
	background-color: #2c9a2c;
	color: #FFF;
}

.SearchResultsCard {
  align-items: center;
  background-color: #FFF;
  border: 3px solid #febb00;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  height: 20rem;
  justify-content: flex-start;
  margin: 1.25rem auto;
  padding: 0 0 1rem 0;
  width: 38rem;
  box-shadow: 0px 0px 1px 2px #Feeb00;
}

.course-search-result {
  align-items: center;
  display: flex;
  align-items: center;
  overflow-wrap: break-word;
  height: 2.5rem;
}

.search-result-btn {
	background-color: #febb00;
  border: none;
  border-radius: 10px;
  color: #524949;
  cursor: pointer;
  font-weight: 700;
  margin: 0 0.5rem;   
  outline-width: 0;
}

.search-result-btn:hover {
	background-color: #2c9a2c;
	color: #FFF;
}

.search-result-header {
  color: #524949;
  font-size: 1.5rem;
  font-weight: 600;
  margin: 1rem 0;
}

.nearby-courses {
  color: #2c9a2c;
  font-size: 1.25rem;
  font-weight: 600;
  margin: 0 0 1rem 0;
}
.WeatherCard {
  border: 3px solid #febb00;
  color: #524949;
  width: 20rem;
  text-align: center;
  margin: 1.25rem auto;
  height: 24rem;
  background-color: #FFF;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 0 0 2rem 0;
  box-shadow: 0px 0px 1px 2px #Feeb00;
}

.course-weather-container {
  width: 50%;
  margin: 0 auto;
  display: flex;
  justify-content: center;
}

.current-weather-icon {
  width: 6rem;
}

.weather-card-header {
  font-size: 1.25rem;
  color: #2c9a2c;
  font-weight: 600;
  overflow-wrap: break-word;
  margin: 2rem 0;
}

.weather-info-header {
  font-weight: 700;
}

.weather-info {
  margin: 0.6rem 0;
}

.page-name {
	font-size: 1.5rem;
	color: #524949;
	margin: 1rem 0;
	font-weight: 700;
}

.search-input {
	border: 3px solid #febb00;
	border-radius: 20px;
	border-style: solid;
  box-shadow: 0px 0px 1px 2px #Feeb00;
	font-family: 'Poppins', sans-serif;
	font-size: 1rem;
	font-weight: 600;
	height: 3rem;
  letter-spacing: .05rem;
  outline-width: 0;
	padding: 0 1rem 0 1rem;
	width: 35%;
}

.search-input::-webkit-search-cancel-button{
  cursor: pointer;
}

.submit-btn {
	background-color: #febb00;
  border: none;
  border-radius: 20px;
  color: #524949;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 700;
	height: 2rem;
  margin: 0 0 0 0.5rem;		
  outline-width: 0;
  padding: 0 1.25rem;
	text-shadow: #000 0 0 0;
}

.submit-btn:hover {
	background-color: #2c9a2c;
	color: #FFF;
}

.back-to-search-link {
	color: #2c9a2c;
  text-decoration: none;
  font-weight: 400;
}

.back-to-search-link:hover {
	color: #302a2a;
}

.fa-caret-left {
	margin: 0 0.25rem 0 0;
}
.Home {
  align-items: center;
  background-color: #FFF;
  border: 3px solid #febb00;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  height: 15rem;
  justify-content: center;
  margin: 1.25rem auto;
  padding: 0.75rem 0 1rem 0;
  width: 38rem;
  box-shadow: 0px 0px 1px 2px #Feeb00;
}

.home-header {
  margin: 0 0 1rem;
  font-size: 1.5rem;
}

.home-intro {
  margin: 1rem 4rem;
  line-height: 1.25rem;
}

.home-search-button {
  background-color: #2c9a2c;
  border: none;
  border-radius: 20px;
  color: #FFF;
  cursor: pointer;
  font-size: 1.25rem;
  font-weight: 700;
  height: 2.5rem;
  margin: 1rem 0 0 0.5rem;
  outline-width: 0;
  padding: 0 1.25rem;
  text-shadow: #000 0 0 0;
}

.home-search-instructions {
  font-size: 1.25rem;
  font-weight: 600;
  color: #2c9a2c;
  margin: 1rem 4rem 0;
}

.home-search-button:hover {
  background-color: #febb00;
  color: #524949;
}
